routerequests<template>
  <div class="row">
    <div class="col-xs-12 col-md-12">
      <card header-text="Route Requests">
        <div class="table-responsive">
          <table class="table table-striped first-td-padding">
            <thead>
              <tr>
                <td>Train Name</td>
                <td>Starting Segment</td>
                <td>Ending Segment</td>
                <td>Status</td>
                <td>Accept</td>
                <td>Reject</td>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="routerequest in this.system.system_RouteRequestArray"
                :key="routerequest.trainid"
              >
                <td>{{ routerequest.trainid }}</td>
                <td>{{ routerequest.startingsegment }}</td>
                <td>{{ routerequest.endingsegment }}</td>
                <td>{{ routerequest.status }}</td>
                <td>
                  <input
                    class="btn btn-success"
                    type="submit"
                    value="Accept Route"
                    @click="AcceptRouteHandler(routerequest.trainid)"
                  >
                </td>
                <td>
                  <input
                    class="btn btn-success"
                    type="submit"
                    value="Reject Route"
                    @click="RejectRouteHandler(routerequest.trainid)"
                  >
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </card>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  name: "routerequests",
  components: {},
  computed: {
    ...mapState({
      system: state => state.system
    })
  },
  mounted() {
    this.getSystemRouteRequest();
  },
  methods: {
    ...mapActions("system", ["getSystemRouteRequest"])
  }
};
</script>


